* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

aside {
  width: 300px;
  overflow-y: hidden;
  background-color: rgb(0, 0, 0, 0.77);
  outline: 1px solid #000000;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 4;
  -webkit-transition: height linear 100ms;
  transition: height linear 100ms;
box-shadow: 0px 2px 15px 0px rgba(0,0,0,0.75);
}

.full-height {
  height: 100vh;
}

.min-height {
  height: 40px;
}

input {
  width: 100%;
  height: 50px;
  padding: 15px;
  font-size: 22px;
  outline: 1px solid black;
  border: none;
  background-color: #3e444e;
  color: white;
}

input:hover, input:focus {
  background-color: #555d69;
}

.container {
  width: 100%;
  overflow: auto;
}

.hide {
  height: 0px;
}

.show {
  height: auto;
}


ul {
  margin: 10px 0px;
  list-style: none;
  font-size: 20px;
  overflow: auto;
}

li {
  padding: 10px;
}

li span {
  cursor: pointer;
}

li .none {
  color: rgb(177, 177, 177);
}

li .highlight {
  color: white;
  text-decoration: underline;
}

li .none:hover {
  color: rgb(223, 222, 222);
}

.toggle {
  width: 100%;
  flex: 0 0 40px;
  background-color:#282c34;
  color: white;
  border: none;
}

.toggle:hover, .toggle:focus {
  background-color: #3e444e; 
}

.toggle .icon {
  font-size: 40px;
}

div {
  margin: 0px;
}

@media screen and (max-width: 600px) {
  aside {
    width: 100%;
    
  }
}
.info-window {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  margin: 0px;
  font-size: 17px;
  color: black;
  font-weight: 400;
}

.info-window img {
  width: 200px;
  margin: 10px 0px;
  border-radius: 10px;
}

small {
  font-size: 10px;
}
#map {
  width: 100vw;
  height: 100vh;
}
.modal {
  position: absolute;
  top: 30vh;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 300px;
  z-index: 5;
  padding-bottom: 20px;
  border-radius: 10px;
  background-color: #3e444e;
  box-shadow: 0px 2px 16px 0px rgba(0,0,0,0.73);
  text-align: center;
  color: honeydew;
}

.err-head {
  font-size: 30px;
}

.err-meg {
  font-size: 20px;
}

.err-btn {
  height: 30px;
  background-color: honeydew;
  color: #3e444e;
  font-weight: bold;
}
.App {
  text-align: center;
}

main {
  position: relative;
  display: flex;
  flex-direction: row;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
